/* NEW */
nav {
  width: 540px;
  height: 190px;
  background: transparent;
  padding: 0;
  text-align: right;
}
nav ul {
  list-style: none;
  margin-top: 95px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: nowrap;
}
nav a {
  color: #e65c43;
  text-decoration: none;
  letter-spacing: -0.01em;
  font-size: 0.8em;
  white-space: nowrap;
}
nav a.active {
  border-bottom: 2px solid #e65c43;
  padding-bottom: 2px;
}
nav a:hover {
  color: #fff;
}
@media screen and (max-width: 959px) {
  nav {
    width: auto;
    height: 50px;
    background: transparent;
    padding: 0;
    text-align: center;
  }
  nav ul {
    margin-top: 16px;
    gap: 16px;
    flex-wrap: wrap;
  }
  nav a {
    font-size: 1em;
  }
}
@media screen and (max-width: 767px) {
  nav ul {
    flex-wrap: wrap; /* allow wrapping if needed */
    gap: 12px;
  }
  nav a {
    font-size: 0.9em;
  }
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  line-height: 2em;
  background: url("../../images/bg.gif") #fff repeat-x;
  margin: 0;
}

main {
  width: 940px;
  margin: 0 auto;
  padding: 0 5px;
  padding: 0 10px;
}

figure {
  width: 400px;
  height: 190px;
  background: transparent;
  padding: 0;
  text-align: left;
  background: url("../../images/logo.png") 0 50px no-repeat;
  float: left;
  display: block;
  margin: 0;
}

header {
  width: 100%;
  height: auto;
  background: transparent;
  padding: 0;
  text-align: center;
  clear: both;
  padding-top: 40px;
  padding-bottom: 30px;
}
header img {
  max-width: 100%;
  height: auto;
}
header h1 {
  color: #e65c43;
  font-weight: 100;
  font-size: 4em;
  line-height: 0;
}
header h2 {
  color: #fff;
  font-weight: 900;
  font-size: 2.5em;
  line-height: 0;
  margin-bottom: 70px;
}
header h3 {
  color: #666;
  font-weight: 400;
  font-size: 2em;
  line-height: 0;
  margin-bottom: 50px;
}
header img.hero {
  display: block;
  max-width: 90%;
  height: auto;
  margin: 10px auto 0;
}
@media screen and (max-width: 767px) {
  header h1 {
    font-size: 2em;
  }
  header h2 {
    font-size: 1em;
  }
  header h3 {
    font-size: 1.5em;
  }
}

#headline {
  width: 100%;
  height: 130px;
  background: #f25c43;
  padding: 0;
  text-align: center;
}
#headline h1 {
  color: #fff;
  font-size: 2.5em;
  font-weight: 100;
  padding-top: 50px;
}

#content {
  width: 940px;
  margin: 0 auto;
  padding: 0 5px;
}
#content h3 {
  color: #666;
  font-size: 2em;
  font-weight: 400;
}
#content #products {
  width: 460px;
  float: left;
}
#content #products img {
  float: left;
  margin-right: 20px;
}
#content #products article {
  margin-bottom: 36px;
}
#content #products h4 {
  line-height: 1em;
}
#content #testimonials {
  width: 460px;
  float: right;
}
#content #testimonials p {
  margin-bottom: 18px;
}

.greybar {
  width: 100%;
  height: 90px;
  background: #f5f6f6;
  padding: 0;
  text-align: left;
  clear: both;
  padding-top: 30px;
}

footer {
  width: 100%;
  height: 120px;
  background: #30353d;
  padding: 0;
  text-align: center;
  color: #fff;
  padding-top: 50px;
}

.social-media {
  margin: 0 auto;
  width: 165px;
}
.social-media div {
  float: left;
  margin-right: 5px;
}
.social-media a {
  display: block;
  width: 36px;
  height: 36px;
  text-indent: -9999px;
}
.social-media .one {
  background: url("../../images/social-media.png") 0 0 no-repeat;
}
.social-media .two {
  background: url("../../images/social-media.png") -38px 0 no-repeat;
}
.social-media .three {
  background: url("../../images/social-media.png") -76px 0 no-repeat;
}
.social-media .four {
  background: url("../../images/social-media.png") -114px 0 no-repeat;
}

@media screen and (max-width: 959px) {
  main {
    width: 768px;
  }
  #content {
    width: auto;
  }
}
@media screen and (max-width: 767px) {
  main {
    width: 460px;
  }
  #content #products,
  #content #testimonials {
    width: 96%;
    margin: 0 2%;
    float: none;
  }
}
@media screen and (max-width: 459px) {
  main {
    width: 300px;
  }
  figure {
    background-size: contain;
    height: 100px;
  }
  #headline h1 {
    font-size: 1.5em;
    padding-top: 30px;
  }
  .greybar,
  article img {
    display: none;
  }
}